.card {
  width: 200px;

  padding: 32px 16px 16px;

  background-color: var(--color-white);
  border: 1px solid var(--color-concrete);
  border-radius: 5px;
}

.card__image {
  display: block;

  width: 160px;
  height: 134px;

  margin: 0 auto 40px;

  object-fit: contain;
}

.card__heading {
  margin: 0 0 4px;

  color: var(--color-accent);
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

.card__product-code {
  margin: 0 0 16px;

  color: var(--color-secondary);
  font-size: 10px;
}

.card__rate {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 24px;
}

.card__reviews {
  margin: 0;
  color: var(--color-accent);
  font-size: 10px;
}

.card__price-row {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin: 0 0 16px;
}

.card__price-label {
  color: var(--color-secondary);
  font-size: 12px;
}

.card__price {
  color: var(--color-accent);
  font-weight: 700;
  font-size: 16px;
}

.card__link {
  display: flex;
  align-items: center;
  justify-content: center;

  /* При padding: 10px 68px не проходять тести */
  padding: 10px 68px 11px;

  border-radius: 5px;
  border: 1px solid var(--color-blue);
  background-color: var(--color-blue);

  color: var(--color-white);
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;

  transition: color 0.3s ease, background-color 0.3s ease;
}

.card__link:hover {
  background-color: var(--color-white);
  color: var(--color-blue);
}
